.container {
    min-height: 100vh;
    width: 100%;
    position: fixed;
    display: flex;
    top: 0;
    left: 0;
    z-index: 7;
    pointer-events: none;
}

.container div {
    flex: 1;
}

.backgroundBox {
    display: inline-block;
    text-align: right;
    height: 100%;
    width: 100%;
}